// =================================
//              LOGO
// =================================
.logo {
  height: 3.25rem;
  margin-left: 2rem;
}

// =================================
//              SEARCH
// =================================
.search {
  flex: 0 0 40%;

  display: flex;
  align-items: center;
  justify-content: center;

  @media only screen and (max-width: $bp-smallest) {
    order: 1;
    flex: 0 0 100%;
  }

  &__input {
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: var(--color-grey-light-2);
    border: 0;
    border-radius: 10rem;
    padding: 0.7rem 2rem;
    width: 90%;
    margin-right: -3.25rem;
    transition: all 0.2s;
    cursor: pointer;

    &:focus {
      width: 100%;
      outline: none;
      background-color: var(--color-grey-light-3);
    }

    &::-webkit-input-placeholder {
      color: var(--color-grey-light-4);
      font-weight: 100;
    }
  }

  &__input:focus + &__button {
    background-color: var(--color-grey-light-3);
  }

  &__button {
    border: 0;
    background-color: var(--color-grey-light-2);

    &:focus {
      outline: none;
    }

    &:active {
      transform: translateY(2px);
    }
  }

  &__icon {
    height: 2rem;
    width: 2rem;
    fill: var(--color-grey-dark-3);
  }
}

// =================================
//         USER NAVIGATION
// =================================
// User navigation
.user-nav {
  display: flex;
  align-items: center;
  align-self: stretch;

  & > * {
    display: flex;
    align-items: center;
    padding: 0 2rem;
    height: 100%;
    cursor: pointer;
  }

  & > *:hover {
    background-color: var(--color-grey-light-2);
  }

  &__icon-box {
    display: flex;
    align-items: center;
    position: relative;
  }

  &__icon {
    height: 2.25rem;
    width: 2.25rem;
    fill: var(--color-grey-dark-2);
  }

  &__notification {
    font-size: 0.8rem;
    height: 1.75rem;
    width: 1.75rem;
    border-radius: 50%;
    background-color: var(--color-primary);
    color: #fff;

    position: absolute;
    top: 1.5rem;
    right: 1.1rem;

    // center the text
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__user-photo {
    height: 3.75rem;
    width: 3.5rem;
    border-radius: 50%;
    margin-right: 1rem;
  }
}

// =================================
//         SIDE NAVIGATION
// =================================
.side-nav {
  font-size: 1.4rem;
  list-style: none;
  margin-top: 3.5rem;

  @media only screen and (max-width: $bp-medium) {
    display: flex;
    margin: 0;
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-bottom: 0.5rem;

      @media only screen and (max-width: $bp-medium) {
        margin-bottom: 0;
      }
    }

    @media only screen and (max-width: $bp-medium) {
      flex: 1;
    }
  }

  &__item::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--color-primary);
    height: 100%;
    width: 3px;
    transform: scaleY(0);
    transform-origin: bottom;
    transition: transform 0.2s, width 0.3s cubic-bezier(1, 0, 0, 1) 0.2s,
      background-color 0.1s;
  }

  &__item:hover::before,
  &__item--active::before {
    transform: scaleY(1);
    width: 100%;
  }

  &__item:active::before {
    background-color: var(--color-primary-light);
  }

  &__link:link,
  &__link:visited {
    color: var(--color-grey-light-1);
    text-decoration: none;
    text-transform: uppercase;
    display: block;
    padding: 1.5rem 3rem;
    position: relative;
    z-index: 10;

    display: flex;
    align-items: center;

    @media only screen and (max-width: $bp-medium) {
      justify-content: center;
      padding: 2rem;
    }

    @media only screen and (max-width: $bp-small) {
      flex-direction: column;
      padding: 1.5rem;
    }
  }

  &__icon {
    width: 1.75rem;
    height: 1.75rem;
    margin-right: 2rem;
    fill: currentColor;

    @media only screen and (max-width: $bp-small) {
      margin-right: 0;
      margin-bottom: 7px;
      width: 1.5rem;
      height: 1.5rem;
    }
  }
}

// =================================
//          LEGAL TEXT
// =================================
.legal {
  font-size: 1.2rem;
  text-align: center;
  padding: 2.5rem;
  color: var(--color-grey-light-4);

  @media only screen and (max-width: $bp-largest) {
    display: none;
  }
}

// =================================
//          GALLERY
// =================================
.gallery {
  display: flex;

  &__photo {
    width: 100%;
    display: block;
  }
}

// =================================
//          HOTEL OVERVIEW
// =================================
.overview {
  display: flex;
  align-items: center;
  border-bottom: var(--border-line);

  @media only screen and (max-width: $bp-mobile) {
    flex-direction: column;
  }

  &__heading {
    font-size: 2.25rem;
    font-weight: 300;
    letter-spacing: 1px;
    padding: 1.5rem 3rem;

    @media only screen and (max-width: $bp-small) {
      font-size: 1.75rem;
      padding: 1.25rem 2rem;
    }

    @media only screen and (max-width: $bp-mobile) {
      font-size: 2.5rem;
      padding: 1rem;
    }
  }

  &__stars {
    margin-right: auto;
    display: flex;

    @media only screen and (max-width: $bp-mobile) {
      margin: -1rem 0 0;
    }
  }

  &__icon-star,
  &__icon-location {
    width: 1.75rem;
    height: 1.75rem;
    fill: var(--color-primary);

    @media only screen and (max-width: $bp-mobile) {
      width: 2.75rem;
      height: 2.75rem;
      margin-bottom: 1rem;
    }
  }

  &__location {
    font-size: 1.2rem;
    display: flex;
    align-items: center;

    @media only screen and (max-width: $bp-mobile) {
      font-size: 1.75rem;
      margin-bottom: 1rem;
    }
  }

  &__icon-location {
    margin-right: 0.5rem;
  }

  &__rating {
    background-color: var(--color-primary);
    color: #fff;
    margin-left: 3rem;
    align-self: stretch; //overrides justiy content declaration from the parent element
    padding: 0 2.25rem;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media only screen and (max-width: $bp-small) {
      padding: 0 1.5rem;
    }

    @media only screen and (max-width: $bp-mobile) {
      padding: 1rem;
      width: 100%;
      margin: 0 auto;
    }

    &-average {
      font-size: 2.25rem;
      font-weight: 300;
      margin-bottom: -3px;

      @media only screen and (max-width: $bp-small) {
        font-size: 1.75rem;
        font-weight: 300;
      }

      @media only screen and (max-width: $bp-mobile) {
        font-size: 2.75rem;
      }
    }

    &-count {
      font-size: 0.8rem;
      text-transform: uppercase;

      @media only screen and (max-width: $bp-small) {
        font-size: 0.75rem;
      }

      @media only screen and (max-width: $bp-mobile) {
        font-size: 1rem;
      }
    }
  }
}

// =================================
//          BUTTON INLINE
// =================================
.btn-inline {
  border: none;
  color: var(--color-primary);
  font-size: inherit;
  background-color: transparent;
  border-bottom: 1px solid currentColor;
  padding-bottom: 2px;
  display: inline-block;
  cursor: pointer;
  transition: all 0.2s;

  & span {
    margin-left: 3px;
    transition: maergin-left 0.2s;
  }

  &:hover {
    color: var(--color-grey-dark-1);

    span {
      margin-left: 8px;
    }
  }

  &:focus {
    outline: none;
    animation: var(--animate-pulsate);
  }
}

@keyframes pulsate {
  0% {
    transform: scale(1);
    box-shadow: none;
  }

  50% {
    transform: scale(1.05);
    box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.25);
  }

  100% {
    transform: scale(1);
    box-shadow: none;
  }
}

// =================================
//          PARAGRAPH
// =================================
.paragraph:not(:last-of-type) {
  margin-bottom: 2rem;
}

// =================================
//            LIST
// =================================
.list {
  list-style: none;
  margin: 3rem 0;
  padding: 3rem 0;
  border-top: var(--border-line);
  border-bottom: var(--border-line);

  display: flex;
  flex-wrap: wrap;

  &__item {
    flex: 0 0 50%;
    margin-bottom: 0.7rem;
  }

  &__item::before {
    content: '';
    display: inline-block;
    height: 1rem;
    width: 1rem;
    margin-right: 0.7rem;

    // Older browser
    background-image: url(../../dist/img/chevron-thin-right.svg);
    background-size: cover;

    // Newer browser - we can use 'masks'
    @supports (-webkit-mask-image: url()) or (mask-image: url()) {
      background-color: var(--color-primary);
      -webkit-mask-image: url(../../dist/img/chevron-thin-right.svg);
      -webkit-mask-size: cover;
      mask-image: url(../../dist/img/chevron-thin-right.svg);
      mask-size: cover;
      background-image: none;
    }
  }
}


// =================================
//          RECOMMEND
// =================================

.recommend {
    font-size: 1.3rem;
    color: var(--color-grey-dark-3);
  
    display: flex;
    align-items: center;
    // justify-content: space-between;
  
    &__count {
      margin-right: auto;
    }
  
    &__friends {
      display: flex;
    }
  
    &__photo {
      height: 4rem;
      width: 4rem;
      border-radius: 50%;
      border: 3px solid #fff;
      box-sizing: content-box;
  
      &:not(:last-child) {
        margin-right: -2rem;
      }
    }
  }
  
  // =================================
  //          REVIEWS
  // =================================
  .review {
    background-color: #fff;
    box-shadow: var(--shadow-light);
    padding: 3rem;
    margin-bottom: 3.5rem;
    position: relative;
    overflow: hidden;
  
    @media only screen and (max-width: $bp-medium) {
      padding: 2rem;
      margin-bottom: 3rem;
    }
  
    &__text {
      margin-bottom: 2rem;
      z-index: 2;
      position: relative;
    }
  
    &__user {
      display: flex;
      align-items: center;
    }
  
    &__photo {
      width: 3.5rem;
      height: 3.5rem;
      border-radius: 50%;
      margin-right: 1.5rem;
    }
  
    &__user-box {
      margin-right: auto;
    }
  
    &__user-name {
      font-size: 1.5rem;
      text-transform: uppercase;
      font-weight: 600;
      margin-bottom: -4px;
    }
  
    &__user-date {
      font-size: 1rem;
      color: var(--color-grey-dark-3);
    }
  
    &__rating {
      font-size: 2.2em;
      font-weight: 300;
      color: var(--color-primary);
    }
  
    &::before {
      content: '\201C';
      position: absolute;
      top: -2.75rem;
      left: -1rem;
      line-height: 1;
      font-size: 20rem;
      color: var(--color-grey-light-2);
      font-family: sans-serif;
      z-index: 1;
    }
  }
  
  // =================================
  //              CTA
  // =================================
  .cta {
    padding: 3.5rem 0;
    text-align: center;
  
    @media only screen and (max-width: $bp-medium) {
      padding: 2.5rem 0;
    }
  
    &__book-now {
      font-size: 2rem;
      font-weight: 300;
      text-transform: uppercase;
      margin-bottom: 2.5rem;
    }
  }
  
  .btn {
    font-size: 1.5rem;
    font-weight: 300;
    color: #fff;
    border: none;
    text-transform: uppercase;
    border-radius: 100px;
    background-image: linear-gradient(
      to right,
      var(--color-primary),
      var(--color-primary-dark)
    );
    position: relative;
    overflow: hidden;
    cursor: pointer;
  
    & > * {
      display: inline-block;
      height: 100%;
      width: 100%;
      transition: all 0.5s;
    }
  
    &__visible {
      padding: 2rem 7.5rem;
    }
  
    &__invisible {
      position: absolute;
      top: -100%;
      left: 0;
      padding: 2rem 0;
    }
  
    &:hover {
      background-image: linear-gradient(
        to left,
        var(--color-primary),
        var(--color-primary-dark)
      );
    }
  
    &:hover &__visible {
      transform: translateY(100%);
    }
  
    &:hover &__invisible {
      top: 0;
    }
  
    &:focus {
      outline: none;
      animation: var(--animate-pulsate);
    }
  }
  
