:root {
    // --color-primary: #eb2f64;
    --color-primary: #eb2f64;
    --color-primary-light: #ff3366;
    --color-primary-dark: #ba265d;

    --color-grey-light-1: #faf9f9;
    --color-grey-light-2: #f4f2f2;
    --color-grey-light-3: #f0eeee;
    --color-grey-light-4: #ccc;

    --color-grey-dark-1: #333;
    --color-grey-dark-2: #777;
    --color-grey-dark-3: #999;

    --shadow-dark: 0 2rem 6rem rgba(0,0,0, .3);
    --shadow-light: 0 2rem 5rem rgba(0,0,0, .06);

    --border-line: 1px solid var(--color-grey-light-2);

    --animate-pulsate: pulsate 1s infinite;
}

// Media Queries
$bp-largest: 75em; // 1200px / 16px
$bp-large: 68.75em; // 1100px / 16px
$bp-medium: 56.25em; // 900px / 16px
$bp-small: 37.5em; // 600px / 16px
$bp-smallest: 31.25em; // 500px / 16px
$bp-mobile: 25em; // 400px / 16px


* {
    margin: 0;
    padding: 0;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;

   @media only screen and (max-width: $bp-large) {
       font-size: 50%;
   }
}

body {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    line-height: 1.6;
    color: var(--color-grey-dark-2);
    background-image: linear-gradient(to right bottom,
            var(--color-primary-light),
            var(--color-primary-dark));
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
}